* {
  margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "IBM Plex Sans";
    transition: 0.3s ease-in-out;

    /* Web App Feel */
    outline: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-user-select: none;
    user-select: none;

    font-family: "IBM Plex Sans";
}

