/* HOME PAGE CONTAINER */

.main-container{
    padding: 16px;
}

/* LOCATION SEARCH COMPONENT */


.location-search-container {
    padding-top: 8px;
    display: flex;
    flex-direction: column;
}
.location-search-container .bike-container {
    width: 36px;
    margin-right: 6px;
}
.location-search-container .bike-container img {
    width: 100%;
}
.location-search-container .location-container {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}
.location-search-container .location-container h1 {
    font-size: 16px;
    color: #2e3333;
}
.location-search-container .location-container p {
    font-weight: 400;
    color: #a9a9a9;
    font-size: 14px;
}
.location-search-container .button-container {
    margin-bottom: 12px;
}
.location-search-container .button-container button {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 4px;
    padding-bottom: 4px;
    border-radius: 50px;
    border: 0;
    color: white;
    font-weight: 700;
    font-size: 13px;
}
.location-search-container .button-container button:nth-child(1) {
    background-color: #00ccbc;
}
.location-search-container .button-container button:nth-child(2) {
    background-color: transparent;
    color: #00ccbc;
    font-weight: 500;
}
.location-search-container .search-container {
    display: flex;
    align-items: center;
    margin-bottom: 32px;
}
.location-search-container .search-container input {
    flex: 1;
    margin-right: 8px;
    height: 46px;
    background-color: #f2f2f2;
    border: 0;
    padding-left: 16px;
    font-size: 14px;
    user-select: auto !important;
    -webkit-user-select: auto !important;
}
.location-search-container .search-container input::placeholder {
    color: #c1c1c1;
}
.location-search-container .search-container .filter-button {
    padding: 8px 16px;
}
.location-search-container .search-container .filter-button svg {
    fill: #00ccbc;
    width: 26px;
}


/* FOOD CATEGORY COMPONENT */

.category-container {
    display: flex;
    overflow: scroll;
}
.category-container::-webkit-scrollbar {
    display: none;
}
.category-container .category-box {
    height: 88px;
    min-width: 88px;
    background-color: #00ccbc;
    margin-right: 16px;
    display: flex;
    align-items: flex-end;
    padding: 8px;
    border-radius: 4px;

    background-size: cover;
}
.category-container .category-box h1 {
    font-size: 16px;
    color: white;
}

.category-container .category-box:nth-child(2){
    background-size: 200px;
    background-position: 10%;
}


/* RESTAURANT CARD */

.restaurant-box .image {
    height: 193px;
    width: 100%;
    background-size: cover;
    background-position: center;
    border-radius: 5px;
    margin-top: 16px;
    position: relative;
}

.restaurant-box h1 {
    font-size: 16px;
    margin-top: 16px;
}
.restaurant-box .notice {
    background-color: white;
    position: absolute;
    top: 85%;
    right: 5%;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 50px;
    font-size: 14px;
    font-weight: 600;
    box-shadow: 0 10px 20px #f2f2f2, 0 6px 6px #f2f2f2;
}
.restaurant-box .food-type-container {
    display: flex;
    font-size: 14px;
    color: #a9a9a9;
    align-items: center;
}
.restaurant-box .food-type-container p {
    margin-right: 5px;
}
.restaurant-box .food-type-container svg {
    width: 15px;
    fill: #007e8a;
    margin-right: 5px;
}
.restaurant-box .food-type-container .rating {
    color: #007e8a;
}


.heading{
        font-size: 20px;
        margin-top: 32px;
}